import React, {useState} from 'react';
import "./TravelPage.css"
import {useDispatch, useSelector} from "react-redux";
import Rodal from "rodal";
import {addEnquiry, changeVisible} from "../../../store/slices/EnquirySlice";
import "rodal//lib/rodal.css"
import {toast} from "react-toastify";
import ApiCallSrc from "../../../apiCall/ApiCallSrc";

function TravelPage() {
    const tour = useSelector(state => state.tour.tour);
    const visible = useSelector(state => state.enquiry.visible);
    const dispatch = useDispatch()
    const [firstName, setFirstName] = useState('')
    const [lastName, setLastname] = useState('')
    const [phone, setPhone] = useState('')
    const [email, setEmail] = useState('')
    const [text, setText] = useState('')

    function sendEnquiry() {
        if(firstName && lastName && phone && email && text) {
            dispatch(addEnquiry({firstName,lastName,phone,text,email,tourId:tour.id}))
            dispatch(changeVisible())
            setFirstName("")
            setLastname("")
            setText("")
            setEmail("")
            setPhone("")
        }
    }

    return (
        <div>
            <div className={"textDiv"}>
                <h3 className={"textUzb"}>{tour.title}</h3>
            </div>
            <div className={"uzbFooterDiv"}>
                <h3 className={"h3UzbFooter"}>{tour.description}</h3>
                <h3 className={"h3UzbFooter"}>{tour.description1}</h3>

                <h3 className={"h3UzbFooter2"}>{tour.details}</h3>
                <h3 className={"h3UzbFooter"}>{tour.tourDay} kun</h3>
                <div className={"d-flex"}>
                    <h3 className={"h3UzbFooter4"}>Price: </h3>
                    <h3 className={"h3UzbFooter3"}> {tour.price} $</h3>
                </div>
                <button onClick={()=>{dispatch(changeVisible())}} className={"buttonOrder2"}>Order</button>
            </div>
            <div className={"divFoto"}>
                <div>
                    {tour && tour.videos && tour.videos.length > 0 && (
                    <video className="video" controls>
                            <source src={ApiCallSrc()+`${tour.videos[0].path}`}
                                type="video/mp4"/>
                            </video>
                    )}
                            </div>
                <div>
                    <img className={"image"}
                         src={tour ? ApiCallSrc()+ tour.images[0].path : ""}/>
                </div>
            </div>

            <Rodal width={550} height={380} visible={visible} onClose={() => {
                dispatch(changeVisible())
            }}>
                <div className="d-flex flex-column mt-4">
                    <div className="d-flex gap-3">
                        <input type="text" onChange={(e)=>{setFirstName(e.target.value)}} placeholder={"LastName..."} className={"form-control b"}/>
                        <input type="text" onChange={(e)=>{setLastname(e.target.value)}} placeholder={"FirstName..."} className={"form-control b"}/>
                    </div>
                    <div className="d-flex gap-3">
                        <input type="number" onChange={(e)=>{setPhone(e.target.value)}} placeholder={"Phone Number..."} value={phone} className={"form-control b"} maxLength={13}/>
                        <input type="email" onChange={(e)=>{setEmail(e.target.value)}} placeholder={"Email..."} className={"form-control b"}/>
                    </div>
                    <textarea onChange={(e)=>{setText(e.target.value)}} rows="6" placeholder={"Text..."} className={"form-control b"}></textarea>
                    <div className="">
                        <button style={{width:"80px",height:"40px"}} onClick={()=> {
                            sendEnquiry()
                        }} className={"float-end"}>Send</button>
                    </div>
                </div>
            </Rodal>

        </div>
    );
}

export default TravelPage;