import {createSlice} from "@reduxjs/toolkit";

const tourSlice = createSlice({
    name: "tour",
    initialState: {
        tours: [],
        tour:""
    },

    reducers: {
        addTour: (state, action) => {
            state.tours.push(action.payload)
        },
        getTour: (state, action) => {
            state.tours = action.payload
        },
        getTourAdmin: (state, action) => {
            state.tours = action.payload
        },
        deleteTour: (state, action) => {
            state.tours.remove(action.payload);
        },
        updateTour: (state, action) => {
            state.tours = state.tours
        },
        updateTourWithImage: (state, action) => {
            state.tours = state.tours
        },
        updateTourWithVideo: (state, action) => {
            state.tours = state.tours
        },
        updateTourWithVideoAndImage: (state, action) => {
            state.tours = state.tours
        },
        getTourById: (state, action) => {
            state.tour = action.payload
        },
    }
})

export const {addTour,getTour,getTourAdmin,deleteTour,updateTour,getTourById,updateTourWithImage,updateTourWithVideo,updateTourWithVideoAndImage} = tourSlice.actions;
export default tourSlice;