import React, {useEffect, useState} from 'react';
import './Connection.css';
import {useDispatch, useSelector} from "react-redux";
import {addEnquiry} from "../../../store/slices/EnquirySlice";
import {toast, ToastContainer} from "react-toastify";

const Connection = () => {
    const dispatch = useDispatch();
    const tour = useSelector((state)=>state.tour)
    const [text, setText] = useState('')
    const [lastName, setLastName] = useState('')
    const [firstName, setFirstName] = useState('')
    const [phone, setPhone] = useState('')
    const [email, setEmail] = useState('')
    const [tourId, setTourId] = useState('')
    function addEnquiry2(e){
        e.preventDefault()
        if(lastName && firstName && phone && email && text && tourId){
            let enquiry= {
                lastName, firstName, phone, email, text, tourId
            }
            dispatch(addEnquiry(enquiry))
            setLastName("")
            setFirstName("")
            setText("")
            setPhone("")
            setEmail("")
            setTourId("")
        }
    }
    return (
        <div className="connection-form" id={"Contact_Us"}>
            <h2>Connection</h2>
            <form onSubmit={addEnquiry2} className={"form"}>
                <div className="form-group my-2">
                    <div className="flex">
                        <input onChange={(e)=>setLastName(e.target.value)} type="text" placeholder="Lastname" className={"form-control-lg"} value={lastName}/>
                        <input onChange={(e)=>setPhone(e.target.value)} type="number" placeholder="Phone" className={"form-control-lg"} value={phone} />
                    </div>
                    <div className="flex">
                        <input onChange={(e)=>setFirstName(e.target.value)} type="text" placeholder="Firstname" className={"form-control-lg"} value={firstName}/>
                        <input onChange={(e)=>setEmail(e.target.value)} type="email" placeholder="Email" className={"form-control-lg"} value={email} />
                    </div>
                </div>
                <textarea onChange={(e)=>setText(e.target.value)} style={{height:115}} placeholder="Text" className={"form-control-lg"} value={text}></textarea>
                <div className="form-group">
                    <select value={tourId} onChange={(e)=>setTourId(e.target.value)} className={"form-select-lg"}>
                         <option value={""} selected disabled>Tanlang</option>
                        {
                            tour.tours.map((t,i)=><option value={t.id} key={t.id}>
                                {t.title}
                            </option>)
                        }
                    </select>
                    <button className={"buttonOrder2"}>Send</button>
                </div>
            </form>
            <ToastContainer
                position="top-right"
                autoClose={3000}
                hideProgressBar={false}
                closeOnClick
                draggable
                pauseOnHover={false}
                theme="dark"
            />
        </div>
    );
};

export default Connection;