import React, {useEffect, useState} from 'react';
import {useDispatch, useSelector} from "react-redux";
import {getEnquiry, sendEnquiry} from "../../../store/slices/EnquirySlice";
import {ToastContainer} from "react-toastify";
import Rodal from "rodal";

const Enquiries = () => {
    const dispatch = useDispatch()
    const enquiries = useSelector(state => state.enquiry.enquiries)
    const [visible, setVisible] = useState(false)
    const [text, setText] = useState("")
    const [subject, setSubject] = useState('')
    useEffect(() => {
        dispatch(getEnquiry())
    }, []);

    function send() {
        let a = {
            text, subject, enquiryId: localStorage.getItem("enquiryId")
        }
        dispatch(sendEnquiry(a))
        setVisible(false)
    }

    function openModal(id) {
        setVisible(true)
        localStorage.setItem("enquiryId", id)
    }

    return (
        <div>
            <div className={"border border-1 w-100"}>
                <div className={"d-flex px-4 border-bottom align-items-center"} style={{height:"70px"}}>
                    <p style={{width:"50px",margin:0}}></p>
                    <p style={{width:"170px",margin:0}}>FirstName</p>
                    <p style={{width:"170px",margin:0}}>LastName</p>
                    <p style={{width:"170px",margin:0}}>Phone</p>
                    <p style={{width:"300px",margin:0}}>Email</p>
                    <p style={{width:"330px",margin:0}}>Tour Name</p>
                    <p style={{width:"500px",margin:0}}>Text</p>
                    <p style={{width:"70px",margin:0}}>Actions</p>
                </div>
                {
                    enquiries.map((enquiry, i) => <div className={"border-bottom px-4"}>
                        <div className={"d-flex align-items-center"} style={{height: "70px"}}>
                            <p style={{width: "50px", margin: 0}}>{i + 1}</p>
                            <p style={{width: "170px", margin: 0}}>{enquiry.firstName}</p>
                            <p style={{width: "170px", margin: 0}}>{enquiry.lastName}</p>
                            <p style={{width: "170px", margin: 0}}>{enquiry.phone}</p>
                            <p style={{width: "300px", margin: 0}}>{enquiry.email}</p>
                            <p style={{width: "330px", margin: 0}}>{enquiry.tour.title}</p>
                            <p style={{width: "500px", margin: 0}}>{enquiry.text}</p>
                            <p style={{width: "70px", margin: 0}}>
                                <button onClick={() => openModal(enquiry.id)} className="buttonOrder p-2">Answer
                                </button>
                            </p>
                        </div>
                        {
                            enquiry.emails.map(email => <div className={"d-flex flex-column"}>
                                <span>
                                {email.textMessage}
                            </span>
                            </div>)
                        }
                    </div>)
                }
            </div>


            <Rodal width={600} height={380} onClose={() => {
                setVisible(false)
            }} visible={visible}>
                <div className="div mt-5">
                    <input value={subject} onChange={(e) => setSubject(e.target.value)} placeholder={"subject"}
                           className={"form-control"} style={{borderColor: "coral"}} type={"text"}/>
                    <textarea value={text} onChange={(e) => setText(e.target.value)} rows={5} placeholder={"text..."}
                              required={true} className={"form-control"} style={{borderColor: "coral"}}
                              name="message"></textarea>
                    <button onClick={send} className={"float-end p-2"}>Yuborish</button>
                </div>
            </Rodal>
            <ToastContainer
                position="top-right"
                autoClose={3000}
                hideProgressBar={false}
                closeOnClick
                draggable
                pauseOnHover={false}
                theme="dark"
            />
        </div>
    );
};

export default Enquiries;