
import React, {useEffect, useState} from 'react';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import './Comments.css';
import icon from "./images/icon.svg";
import {addComment, getCommentsByTourId} from "../../../store/slices/CommentSlice";
import {useDispatch, useSelector} from "react-redux";
import {useParams} from "react-router-dom";
import {toast, ToastContainer} from "react-toastify";

const ImageCarousel = () => {
    const {id} = useParams();
    const dispatch = useDispatch();
    const comments = useSelector(state => state.comment.comments);
    const [lastName, setLastName] = useState('');
    const [firstName, setFirstName] = useState('');
    const [text, setText] = useState('');

    useEffect(() => {
        dispatch(getCommentsByTourId(id));
    }, [dispatch, id]);

    const postComment = (e) => {
        e.preventDefault()
        if (lastName && firstName && text) {
            dispatch(addComment({
                        tourId:id,
                        comment: {
                            lastName, firstName, text
                        }
                    }
                )
            )

            setFirstName("")
            setLastName("")
            setText("")
        }else{
            toast.warning("fill the blanks")
        }
    };

    const settings = {
        dots: comments.length > 1,
        infinite: comments.length > 3,
        speed: 500,
        slidesToShow: Math.min(3, comments.length),
        slidesToScroll: 1,
        autoplay: comments.length > 1,
        autoplaySpeed: 3000,
        centerMode:comments.length===1,
        centerPadding:"0"
    };

    return (
        <div>
            <div className={"headerLorem"}>
                <h3 className={"loremText"}>Comments</h3>
            </div>
            <div className="carousel-container">
                {comments.length > 0 ? (
                    <Slider {...settings}>
                        {
                            comments.map(comment => (
                                <div key={comment.id} className="commentCard">
                                    <div>
                                        <img src={icon} alt="User Icon"/>
                                    </div>
                                    <div className={"text-center"}>
                                        <h3 className={"commentName"}>{`${comment.lastName} ${comment.firstName}`}</h3>
                                        <h3 className={"commentName2"}>{comment.tour.title}</h3>
                                    </div>
                                    <div className={"commentFooter"}>
                                        <p className={"commentP"}>
                                            {comment.text}
                                        </p>
                                    </div>
                                </div>
                            ))
                        }
                    </Slider>
                ) : (
                    <p></p>
                )}
            </div>
            <form onSubmit={postComment} className={"divInp"}>
                <input
                    value={lastName}
                    onChange={(e) => setLastName(e.target.value)}
                    type={"text"}
                    className={"input1"}
                    placeholder={"LastName"}
                />
                <input
                    value={firstName}
                    onChange={(e) => setFirstName(e.target.value)}
                    type={"text"}
                    className={"input1"}
                    placeholder={"FirstName"}
                />
                <input
                    value={text}
                    onChange={(e) => setText(e.target.value)}
                    type={"text"}
                    className={"input2"}
                    placeholder={"Text"}
                />
                <button className={"buttonComment"}>
                    <p className={"pSend"}>Send</p>
                </button>
            </form>
            <br/>
            <br/>
            <ToastContainer
                position="top-right"
                autoClose={3000}
                hideProgressBar={false}
                closeOnClick
                draggable
                pauseOnHover={false}
                theme="dark"
            />
        </div>
    );
};
export default ImageCarousel;