import React, {useEffect} from 'react';
import {useDispatch, useSelector} from "react-redux";
import {deleteComment, editComment, getComment} from "../../../store/slices/CommentSlice";

const Comments = () => {
    const dispatch = useDispatch();
    const comments = useSelector((state)=>state.comment.comments)
    useEffect(() => {
        dispatch(getComment())
    }, []);
    return (
        <div>
            <table className="table">
                <thead>
                <tr>
                    <th></th>
                    <th>Lastname</th>
                    <th>Firstname</th>
                    <th>Tour Name</th>
                    <th>Text</th>
                    <th>Actions</th>
                </tr>
                </thead>
                <tbody>
                {
                    comments.map((comment,i) =>
                        <tr key={comment.id}>
                            <td>
                                {i + 1}
                            </td>
                            <td>
                                {comment.lastName}
                            </td>
                            <td>
                                {comment.firstName}
                            </td>
                            <td>
                                {comment.tour.title}
                            </td>
                            <td>
                                {comment.text}
                            </td>
                            <td>
                                <button onClick={(e)=>{dispatch(editComment(comment.id))}} className={"btn btn-dark"}>
                                    <span className="material-symbols-outlined">check</span>
                                </button>
                                <button onClick={()=>{dispatch(deleteComment(comment.id))}} className={"btn btn-danger mx-3"}>
                                    <span className="material-symbols-outlined">delete</span>
                                </button>
                            </td>
                        </tr>
                    )
                }
                </tbody>
            </table>
        </div>
    );
};

export default Comments;