import React from 'react';
import {Link, useNavigate} from "react-router-dom";
import {changeLanguage} from "../../../store/slices/LanguageSlice";
import {useDispatch, useSelector} from "react-redux";
import "../../AdminPage.scss"
import a from "./Vector.svg"
const Header = () => {
    const dispatch = useDispatch();
    const language = useSelector(state=>state.language.language)
    const navigate = useNavigate();
    return (
        <div>
            <div className="header1">
                <img src={a} className={"logo_image"} alt=""/>
                <b>Lastname Firstname</b>
                <Link to={"/admin/tour"}>Add tour</Link>
                <Link to={"/admin/enquiries"}>Enquiries</Link>
                <Link to={"/admin/comments"}>Comments</Link>
                <Link to={"/admin/available_tours"}>Available Tours</Link>
                <div>
                    <select value={language} onChange={(e) => dispatch(changeLanguage({
                        lang: e.target.value,
                        userId: localStorage.getItem("userId")
                    }))} className={"form-select select-option"}>
                        <option value="LANG1">En</option>
                        <option value="LANG2">Gr</option>
                        <option value="LANG3">Fr</option>
                    </select>
                </div>
                <button onClick={()=>{
                    localStorage.removeItem("access_token")
                    localStorage.removeItem("refresh_token")
                    localStorage.removeItem("userId")
                    navigate("/login")
                }} className={"btn btn-danger"}>Log out</button>
            </div>
        </div>
    );
};

export default Header;