import {createSlice} from "@reduxjs/toolkit";

const tourDaySlice = createSlice({
    name: "tourDay",
    initialState: {
        tourDays: []
    },

    reducers: {
        addTourDay: (state, action) => {
            state.tourDays = action.payload
        },
        getTourDay: (state, action) => {
            state.tourDays = action.payload
        },
        deleteTourDay: (state, action) => {
            state.tourDays = action.payload
        },
        updateTourDay: (state, action) => {
            state.tourDays = action.payload
        },
        updateTourDayWithImage: (state, action) => {
            state.tourDays = action.payload
        },
    }
})

export const {addTourDay,getTourDay,deleteTourDay,updateTourDay,updateTourDayWithImage} = tourDaySlice.actions;
export default tourDaySlice;