import React, {useEffect, useState} from 'react';
import {useDispatch, useSelector} from "react-redux";
import {
    deleteTour,
    getTour, getTourAdmin,
    updateTour,
    updateTourWithImage,
    updateTourWithVideo,
    updateTourWithVideoAndImage
} from "../../../store/slices/TourSlice";
import {useNavigate} from "react-router-dom";
import Rodal from "rodal";
import {ToastContainer} from "react-toastify";
import ApiCallSrc from "../../../apiCall/ApiCallSrc";

const AvailableTours = () => {
    const tours = useSelector((state) => state.tour.tours)
    const [currentTour, setCurrentTour] = useState(localStorage.getItem("currentTour") ? JSON.parse(localStorage.getItem("currentTour")) : "")
    const [visible, setVisible] = useState(false)
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const [videoRef, setVideoRef] = useState()
    useEffect(() => {
        dispatch(getTourAdmin())
    }, []);

    function editTour() {
        if (currentTour) {
            if (currentTour.image && !currentTour.video) {
                dispatch(updateTourWithImage(currentTour))
            } else if (!currentTour.image && currentTour.video) {
                dispatch(updateTourWithVideo(currentTour))
            } else if (currentTour.image && currentTour.video) {
                dispatch(updateTourWithVideoAndImage(currentTour))
            } else {
                dispatch(updateTour(currentTour))
            }
            localStorage.removeItem("currentTour")
            setCurrentTour("");
            setVisible(false)
        }

    }

    return (
        <div>
            <table className="table">
                <thead>
                <tr>
                    <th>Photo</th>
                    <th>Video</th>
                    <th>Title</th>
                    <th>Description1</th>
                    <th>Description2</th>
                    <th>Day</th>
                    <th>Price</th>
                    <th>Edit</th>
                    <th>Delete</th>
                </tr>
                </thead>
                <tbody>
                {
                    tours.map((tour) =>
                        <tr key={tour.id}>
                            <td onClick={() => {
                                navigate("/admin/tourDays/" + tour.id)
                            }}>
                                <img width={380} height={240}
                                     src={ApiCallSrc()+ tour.images[0].path} alt=""/>
                            </td>
                            <td>
                                <video ref={videoRef} src={ApiCallSrc() + tour.videos[0].path} controls width="423" height="238">
                                    <source type="video/mp4"/>
                                    Your browser does not support the video tag
                                </video>
                            </td>
                            <td onClick={() => {
                                navigate("/admin/tourDays/" + tour.id)
                            }}>
                                {tour.title}
                            </td>
                            <td onClick={() => {
                                navigate("/admin/tourDays/" + tour.id)
                            }}>
                                {tour.description}
                            </td>
                            <td onClick={() => {
                                navigate("/admin/tourDays/" + tour.id)
                            }}>
                                {tour.description1}
                            </td>
                            <td onClick={() => {
                                navigate("/admin/tourDays/" + tour.id)
                            }}>
                                {tour.tourDay}
                            </td>
                            <td onClick={() => {
                                navigate("/admin/tourDays/" + tour.id)
                            }}>
                                {tour.price}
                            </td>
                            <td>
                                <button onClick={() => {
                                    setCurrentTour({
                                        title: tour.title,
                                        description: tour.description,
                                        description1: tour.description1,
                                        price: tour.price,
                                        tourDay: tour.tourDay,
                                        id: tour.id,
                                        info: tour.info,
                                        lang: tour.lang
                                    });
                                    localStorage.setItem("currentTour", JSON.stringify(tour))
                                    setVisible(true)
                                }} className={"btn btn-dark"}>
                                    <span className="material-symbols-outlined">edit</span>
                                </button>
                            </td>
                            <td>
                                <button onClick={() => {
                                    dispatch(deleteTour(tour.id))
                                }} className={"btn btn-danger mx-3"}>
                                    <span className="material-symbols-outlined">delete</span>
                                </button>
                            </td>
                        </tr>
                    )
                }
                </tbody>
            </table>

            <Rodal width={800} height={630} visible={visible} onClose={() => setVisible(false)}>
                <div className={"mt-2"}>
                    <div className="d-flex gap-3">
                        <div className="w-100">
                            <label htmlFor="">
                                Image:
                            </label>
                            <input onChange={(e) => {
                                setCurrentTour({...currentTour, image: e.target.files[0]})
                            }} className={"form-control"} style={{borderColor: "coral"}} type="file"/>
                        </div>
                        <div className="w-100">
                            <label htmlFor="">Video: </label>
                            <input onChange={(e) => {
                                setCurrentTour({...currentTour, video: e.target.files[0]})
                            }} className={"form-control"} style={{borderColor: "coral"}} type="file"/>
                        </div>
                    </div>
                    <div className="d-flex">
                        <div className="w-100">
                            <label htmlFor="">Title: </label>
                            <input onChange={(e) => {
                                setCurrentTour({...currentTour, title: e.target.value})
                            }} className={"form-control"} style={{borderColor: "coral"}} type="text"
                                   value={currentTour.title}/>
                        </div>
                    </div>
                    <div className="d-flex gap-3">
                        <div className="w-100">
                            <label htmlFor="">Description 1:</label>
                            <textarea rows={5} onChange={(e) => {
                                setCurrentTour({...currentTour, description: e.target.value})
                            }} className={"form-control"} style={{borderColor: "coral"}}
                                      value={currentTour.description}></textarea>
                        </div>
                        <div className="w-100">
                            <label htmlFor="">Description 2: </label>
                            <textarea rows={5} onChange={(e) => {
                                setCurrentTour({...currentTour, description1: e.target.value})
                            }} className={"form-control"} style={{borderColor: "coral"}}
                                      value={currentTour.description1}></textarea>
                        </div>
                    </div>
                    <div className="d-flex gap-3">
                        <div className="w-100">
                            <label htmlFor="">Day Count: </label>
                            <input onChange={(e) => {
                                setCurrentTour({...currentTour, tourDay: e.target.value})
                            }} className={"form-control"} style={{borderColor: "coral"}} type="number"
                                   value={currentTour.tourDay}/>
                        </div>
                        <div className="w-100">
                            <label htmlFor="">Price: </label>
                            <input onChange={(e) => {
                                setCurrentTour({...currentTour, price: e.target.value})
                            }} className={"form-control"} style={{borderColor: "coral"}} type="number"
                                   value={currentTour.price}/>
                        </div>
                    </div>
                    <div className="d-flex">
                        <div className="w-100">
                            <label htmlFor="">Additional Information</label>
                            <textarea rows={5} onChange={(e) => {
                                setCurrentTour({...currentTour, description1: e.target.value})
                            }} className={"form-control"} style={{borderColor: "coral"}}
                                      value={currentTour.info}></textarea>
                        </div>
                    </div>
                    <button onClick={() => {
                        editTour()
                    }} className={"btn btn-success float-end"}>
                        <span className="material-symbols-outlined">check</span>
                    </button>
                </div>
            </Rodal>
            <ToastContainer
                position="top-right"
                autoClose={3000}
                hideProgressBar={false}
                closeOnClick
                draggable
                pauseOnHover={false}
                theme="dark"
            />
        </div>
    );
};

export default AvailableTours;