import {createSlice} from "@reduxjs/toolkit";

const enquirySlice = createSlice({
    name: "enquiry",
    initialState: {
        enquiries: [],
        visible: false,
       obj:{
           subject:"",
           text:"",
           enquiryId:""
       },
       inpValue:[]
    },

    reducers: {
        addEnquiry: (state, action) => {
            state.enquiries.push(action.payload)
        },
        getEnquiry: (state, action) => {
            state.enquiries = action.payload
        },
        editEnquiry: (state, action) => {
            state.enquiries = state.enquiries
        },
        deleteEnquiry: (state, action) => {
            state.enquiries = state.enquiries
        },
        changeVisible:(state,action)=>{
            state.visible = !state.visible
        },
        sendEnquiry:(state,action)=>{
            console.log(action.payload)
        },

    }
})

export const {sendEnquiry,addEnquiry,changeVisible,getEnquiry,editEnquiry,deleteEnquiry} = enquirySlice.actions;
export default enquirySlice;