import React, {useEffect, useRef, useState} from 'react';
import {useParams} from "react-router-dom";
import {
    addTourDay,
    deleteTourDay,
    getTourDay,
    updateTourDay,
    updateTourDayWithImage
} from "../../../store/slices/TourDaySlice";
import {useDispatch, useSelector} from "react-redux";
import {useForm} from "react-hook-form";
import {toast, ToastContainer} from "react-toastify";
import ApiCallSrc from "../../../apiCall/ApiCallSrc";
import {getTourById} from "../../../store/slices/TourSlice";

const TourDays = () => {
    const {id} = useParams()
    const dispatch = useDispatch();
    const tourDays = useSelector(state => state.tourDay.tourDays)
    const tour = useSelector(state => state.tour.tour)
    const [currentTourDay, setCurrentTourDay] = useState({})
    const [image, setImage] = useState()
    const {handleSubmit, register, reset} = useForm()
    const imageRef = useRef(null);
    useEffect(() => {
        dispatch(getTourDay(id));
        dispatch(getTourById(id));
    }, []);
    const submit = (data) => {
        if (data && image) {
            data = {...data, tourId: id, image}
            dispatch(addTourDay(data))
            reset({
                title: "",
                description: ""
            })
            if (imageRef.current) {
                imageRef.current.value = "";
            }
        } else {
            toast.warning("")
        }

    }

    function editTourDay() {
        if (currentTourDay.image) {
            dispatch(updateTourDayWithImage({currentTourDay, id}))
            setCurrentTourDay({})
        } else {
            dispatch(updateTourDay({currentTourDay, id}))
            setCurrentTourDay({})
        }
    }

    return (
        <div className={"mt-4"}>
            <div className="div">
                <div className={"textDiv"}>
                    <h3 className={"textUzb"}>{tour.title}</h3>
                </div>
                <div className={"uzbFooterDiv w-100"}>
                    <h3 className={"h3UzbFooter"}>{tour.description}</h3>
                    <h3 className={"h3UzbFooter"}>{tour.description1}</h3>

                    <h3 className={"h3UzbFooter2"}>{tour.details}</h3>
                    <h3 className={"h3UzbFooter"}>{tour.tourDay} kun</h3>
                    <div className={"d-flex"}>
                        <h3 className={"h3UzbFooter4"}>Price: </h3>
                        <h3 className={"h3UzbFooter3"}> {tour.price} $</h3>
                    </div>
                </div>
                <div className={"divFoto"}>
                    <div>
                        {tour && tour.videos && tour.videos.length > 0 && (
                            <video className="video" src={ApiCallSrc() + `${tour.videos[0].path}`}
                                   controls>
                                <source
                                    type="video/mp4"/>
                                <p>Video not available</p>
                            </video>
                        )}
                    </div>
                    <div>
                        <img className={"image"}
                             src={tour ? ApiCallSrc()+ tour.images[0].path : ""}/>
                    </div>
                </div>
            </div>
            <form onSubmit={handleSubmit(submit)} className="div border border-1 p-3 rounded d-flex gap-3 flex-column ">
                <label htmlFor="image">Image</label>
                <input ref={imageRef} onChange={event => setImage(event.target.files[0])} className={"form-control"}
                       id={"image"} type="file"/>
                <label htmlFor="title">Title</label>
                <input {...register("title", {required: true})} className={"form-control"} type="text"/>
                <label htmlFor="desc">Description</label>
                <textarea className={"form-control"} {...register("description", {required: true})} rows={4}></textarea>
                <div className="">
                    <button className={"btn btn-primary float-end btn-lg"}>Submit</button>
                </div>
            </form>
            <table className="table mt-5">
                <thead>
                <tr>
                    <th>Photo</th>
                    <th>Title</th>
                    <th>Description</th>
                    <th>Actions</th>
                </tr>
                </thead>
                <tbody>
                {
                    tourDays.map((tourDay) =>
                        currentTourDay.id === tourDay.id ?
                            <tr>
                                <td>
                                    <input onChange={(e) => {
                                        setCurrentTourDay({...currentTourDay, image: e.target.files[0]})
                                    }} className={"form-control"} type="file"/>
                                </td>
                                <td>
                                    <input onChange={(e) => {
                                        setCurrentTourDay({...currentTourDay, title: e.target.value})
                                    }} className={"form-control"} type="text" value={currentTourDay.title}/>
                                </td>
                                <td>
                                    <input onChange={(e) => {
                                        setCurrentTourDay({...currentTourDay, description: e.target.value})
                                    }} className={"form-control"} type="text" value={currentTourDay.description}/>
                                </td>
                                <td>
                                    <button onClick={() => {
                                        editTourDay()
                                    }} className={"btn btn-success"}>
                                        <span className="material-symbols-outlined">check</span>
                                    </button>
                                </td>
                            </tr>
                            :
                            <tr key={tourDay.id}>
                                <td>
                                    <img width={380} height={240}
                                         src={ApiCallSrc() + tourDay.images[0].path} alt=""/>
                                </td>
                                <td>
                                    {tourDay.title}
                                </td>
                                <td>
                                    {tourDay.description}
                                </td>
                                <td>
                                    <button onClick={() => {
                                        setCurrentTourDay({
                                            title: tourDay.title,
                                            description: tourDay.description,
                                            id: tourDay.id,
                                            lang: tourDay.lang
                                        });
                                        localStorage.setItem("currentTourDay", JSON.stringify(tourDay))
                                    }} className={"btn btn-dark"}>
                                        <span className="material-symbols-outlined">edit</span>
                                    </button>
                                    <button onClick={() => {
                                        dispatch(deleteTourDay({tourDayId: tourDay.id, tourId: id})
                                        )
                                    }} className={"btn btn-danger mx-3"}>
                                        <span className="material-symbols-outlined">delete</span>
                                    </button>
                                </td>
                            </tr>
                    )
                }
                </tbody>
            </table>
            <ToastContainer
                position="top-right"
                autoClose={3000}
                hideProgressBar={false}
                closeOnClick
                draggable
                pauseOnHover={false}
                theme="dark"
            />
        </div>
    );
};

export default TourDays;